
import { defineComponent, } from "vue";


export default defineComponent({

  name: 'Toggle',
  props: {

    show: {
      type: Boolean,
      default: true
    },
    
    label: {
      type: String,
      required: true,
    },
    item: {
      type: Boolean,
      required: true,
    },
    color: {
      type: String,
      required: true,
      default: 'white',
    },
  },
  
  data() {
    return {
      selected: this.item,
    };
  },
  methods: {
    onChange() {
      this.$emit('toggle', this.selected );
    },
  },
});
